@import '../../../theme.scss';
.product-card {
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 15px;
  height: 500px;
  width: 275px;
  outline: 1px solid #dbd5d5;
}

.product-name {
  padding: 10px 10px 5px 10px;
  color: black;
  font-weight: bold;
  margin: 0px;
  font-size: 20px;
}
.product-producer-name {
  padding: 5px 10px 10px 10px;
  color: $primary;
  margin: 0px;
  font-weight: 500;
  font-size: 17px;

}